const tranZH = {
    'Danmaku is loading': '',
    'Top': '',
    'Bottom': '',
    'Rolling': '',
    'Input danmaku, hit Enter': '',
    'About author': '',
    'DPlayer feedback': '',
    'About DPlayer': ' DPlayer ',
    'Loop': '',
    'Speed': '',
    'Opacity for danmaku': '',
    'Normal': '',
    'Please input danmaku!': '',
    'Set danmaku color': '',
    'Set danmaku type': '',
    'Danmaku': ''
};

module.exports = function (lang) {
    this.lang = lang;
    this.tran = (text) => {
        if (this.lang === 'en') {
            return text;
        }
        else if (this.lang === 'zh') {
            return tranZH[text];
        }
    };
};


